import Loader from "../components/loader";
import Header from "../components/header";
import AboutUs from "../components/aboutus";
import Services from "../components/services";
import Features from "../components/features";
import Technology from "../components/technology";
import Portfolio from "../components/portfolio";
import WorkWithUs from "../components/workwithus";
import Testimonials from "../components/testimonials";
import Footer from "../components/footer";
import Social from "../components/social";

import { GetData } from "../utils";

// Import CSS files
import main from "../css/main.css";
import style from "../css/style.css";
import heading from "../css/heading.css";
import slick from "../css/slick.css";
import responsive from "../css/responsive.css";

export default function (props) {
  return (
    <>
      <Loader />
      <Header />
      <AboutUs data={props.aboutUS} links={props.socialLinks} />
      <Services data={props.services} />
      <Features data={props.features} />
      <Portfolio data={props.portfolios} />
      <WorkWithUs />
      <Technology data={props.technologies} />
      <Testimonials data={props.services} />
      <Footer />
      <Social />
    </>
  );
}

export async function getStaticProps() {
  return {
    props: {
      aboutUS: await GetData("about-us"),
      socialLinks: await GetData("social-links"),
      services: await GetData("services"),
      features: await GetData("features"),
      portfolios: await GetData("portfolios"),
      technologies: await GetData("technologies"),
      services: await GetData("services"),
    },
  };
}
